.mySwiper {
    top: 0;
    margin-top: 0rem;
    width: 100%;
    height: 100%;
    z-index: 1;
    --swiper-navigation-size: 2rem;
  }
  
.swiper-slide {
    text-align: center;
    font-size: inherit;
    background: inherit;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.swiper-button-next, .swiper-button-prev {
    color: inherit;
    position: fixed;
    top: 50%;
}