
.settings {
    width: 95%;
    margin: 10% auto 10% auto;
    font-size: 1.4rem;
    background: inherit;
}

.settings-item {
    display: flex;
    margin: auto 0 1.5rem 0;
    justify-content: space-between;
    flex-wrap: wrap;
}

.settings-item label {
    width: 50%;
    margin: auto 0 auto 0;
    display: inline-block;
    flex-wrap: wrap;
}

.settings-item select {
    display: inline-block;
    height: fit-content;
    max-width: 50%;
    margin: auto 0 auto 0;
    border-radius: 0.25em;
    font-size: 1.4rem;
}