html {
  font-size: 62.5%;
    --body-text: 1.6rem;
    --light-color: #0f0f0f;
    --dark-color: #8f95a1;
    --sepia-color: #1b0f0b;
    --light-background: #f4f2ff;
    --dark-background: #2c2f37;
    --sepia-background: #eee4df;
    --light-navbar-background: #eeecfd;
    --dark-navbar-background: #32353d;
    --sepia-navbar-background: #e0ccc3;
}


@font-face {
    font-family: "Merriweather-Regular";
    src: local("Merriweather-Regular"),
        url("./fonts/Merriweather/Merriweather-Regular.ttf") format("truetype");
    font-weight: normal;
    font-display: swap;
}

@font-face {
    font-family: "FrederickatheGreat-Regular";
    src: local("FrederickatheGreat-Regular"),
        url("./fonts/FrederickatheGreat/FrederickatheGreat-Regular.ttf") format("truetype");
    font-weight: normal;
    font-display: swap;
}

body {
  font-family: "Merriweather-Regular", sans-serif;
  font-size: var(--body-text);
}

.fontZoomM {
    font-size: var(--body-text);
}

.fontZoomL {
    font-size: large;
}

.fontZoomXL {
    font-size: x-large;
}

.fontZoomXXL {
    font-size: xx-large;
}

.fontZoomXXXL {
    font-size: xxx-large;
}

/* Dark theme */
.nightTheme {
    background: var(--dark-background);
    color: var(--dark-color);
}

.nightTheme .footerbar {
    background: var(--dark-navbar-background);
}

.nightTheme .navbar {
    background: var(--dark-navbar-background);
}

/* Light theme */
.dayTheme {
    background: var(--light-background);
    color: var(--light-color);
}

.dayTheme .footerbar {
    background: var(--light-navbar-background);
}

.dayTheme .navbar {
    background: var(--light-navbar-background);
}

/* Sepia theme */
.sepiaTheme {
    background: var(--sepia-background);
    color: var(--sepia-color);
}

.sepiaTheme .footerbar {
    background: var(--sepia-navbar-background);
}

.sepiaTheme .navbar {
    background: var(--sepia-navbar-background);
}